
export function isAdminUser (token) {
    // eslint-disable-next-line no-magic-numbers
    const base64Url = token.split('.')[1];
    // eslint-disable-next-line require-unicode-regexp
    const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
    const jsonPayload = JSON.parse(decodeURIComponent(window.atob(base64).split('')
        .map(
            (c) => {
                // eslint-disable-next-line no-magic-numbers
                return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
            }
        )
        .join('')));
    return jsonPayload && jsonPayload.is_org_admin;
}

/**
 * 
 * @param {*} value 
 * @param {*} key 
 * @param {*} stateObject 
 * @param {*} updateFunction 
 */
// eslint-disable-next-line max-params
export function setStateValue (value, key, stateObject, updateFunction) {

    const updatedValue = {};
    updatedValue[key] = value;
    const updated = {
        ...stateObject,
        ...updatedValue
    }

    updateFunction(updated);
}

/**
 * 
 * @param {String} input value to be checked based on the provided type
 * @param {String} type effectively and enum used to specify the type of check we need to run 
 * @returns String message to be relayed to the user pertaining to the invalid input
 * // success, warning, error, or default
 */
export function verifyInput (input, type, required) {
    if (required && !input) {
        return {
            status: "error",
            message: 'This field can not be left empty'
        };
    }

    let verified = {};
    let results = '';

    switch (type) {
        case 'GENERAL':
            // eslint-disable-next-line require-unicode-regexp
            results = (/^[.@&]?[a-zA-Z0-9 ]+[ !.@&()]?[ a-zA-Z0-9!()]+/).test(input);


            verified = {
                status: results ? 'default' : 'error',
                message: results ? '' : 'This text is invalid please check for uncommonly used characters'
            }
            break;
        case 'DOMAIN':
            // eslint-disable-next-line require-unicode-regexp
            results = (/(?:[a-z0-9](?:[a-z0-9-]{0,61}[a-z0-9])?\.)+[a-z0-9][a-z0-9-]{0,61}[a-z0-9]/g).test(input)
            verified = {
                status: results ? 'default' : 'error',
                message: results ? '' : 'This field needs to be a valid URL'
            }
            break;
        default:
            verified = {
                status: 'default',
                message: ''
            }
    }
    return verified;


}

/**
 * 
 * @param {Object} selections state object containing all of the users input data
 * @param {Object} validations validations object pre-set to govern what the inputs are intended to be
 * @param {function} setValidations function used to update active validations.
 * @return {boolean} represents the current pass fail state for the provided validation set
 */
export function processAllInputs (selections, validations, setValidations) {
    let failureTriggered = false;
    const update = {};
    Object.keys(validations).forEach(validationKey => {
        const submittedValue = selections && selections[validationKey]
        const submittedType = validations[validationKey].inputType

        const verificationObject = verifyInput(submittedValue, submittedType, true);

        if (verificationObject.status === 'error') {
            failureTriggered = true;
        }

        update[validationKey] = {
            inputType: submittedType,
            status: verificationObject.status,
            message: verificationObject.message
        }
       
    });

    setValidations(update);

    return failureTriggered;
}
