import './UserManagementController.scss';
import React, {useEffect, useState, createContext} from 'react';
import {PageHeader, PageHeaderTitle} from "@redhat-cloud-services/frontend-components/PageHeader";
import {Divider, PageSection} from "@patternfly/react-core";
import UserList from "./UserList";
import AddUser from './modifyUser/AddUser'

export const QueryContext = createContext(null);

const UserManagementController = () => {

    let targetSSO;

    if (window.location.host === "stage.foo.redhat.com:1337" || window.location.host === "console.stage.redhat.com") {
        targetSSO = "https://api.access.stage.redhat.com/account/v1/accounts";
    }
    else {
        targetSSO = "https://api.access.redhat.com/account/v1/accounts";
    }

    // stores the required information produce queries. Gets stuffed into the context for use in other modules.
    const [queryDetails, setQueryDetails] = useState({
        accountAPI: targetSSO,
        accountID: "",
        userToken: ""
    });
    const queryBase = {
        accountAPI: targetSSO,
        accountID: "",
        userToken: "",
    }

    const [displayedPage, setDisplayedPage] = useState({
        userList: true,
        addUser: false,
        editUser: false
    })

    // needed to pass edit target from the userList to the addPage. editTarget will be a string with the user's id
    const [editTarget, setEditTarget] = useState()

    /**
     * pulls the user's account list for this user from the account api service
     */
    const getAccountId = () => {
        // eslint-disable-next-line no-undef
        insights.chrome.auth.getToken().then(token => {
            queryBase.userToken = token;
            // trigger fetch with the existing user's token
            fetch(queryDetails.accountAPI, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + token
                }
            })
                .then(response => response.json())
                .then(data => {
                    const bodyPosition = 0;
                    const accountData = data.body[bodyPosition];
                    queryBase.accountID = accountData.id;
                    setQueryDetails(queryBase);
                })
        });
    }

    /**
     * Passed via props to the userlist to govern the displaying of the addUser page.
     */
    const showAddUserPage = () => {
        setDisplayedPage({
            userList: false,
            addUser: true,
            editUser: false
        })
    }


    /**
     * Passed via props to the userlist to govern the displaying of the addUser page.
     */
    const showEditUserPage = () => {
        setDisplayedPage({
            userList: false,
            addUser: false,
            editUser: true
        })
    }

    /**
     * helpful for back/cancel buttons
     */
    const showUserListPage = () => {
        setDisplayedPage({
            userList: true,
            addUser: false,
            editUser: false
        })
    }

    /**
     * used to run the split functionality of what happens when an edit is triggered.
     * @param userObject
     */
    const handleEditSelection = (userObject) => {
        setEditTarget(userObject)
        showEditUserPage();
    }


    /**
     * conditional statement to handle the async nature of the initial js queries
     */
        // eslint-disable-next-line consistent-return
    const BaseRender = () => {
        if (queryDetails.accountID && queryDetails.accountID.length > 0) {
            if (displayedPage.userList) {
                return <UserList
                            triggerAddUser={showAddUserPage}
                            triggerEditUser={handleEditSelection}
                            triggerCancel={showUserListPage}
                        />;
            }
            else if (displayedPage.addUser) {
                return <AddUser triggerReturn={showUserListPage}/>;
            }
            else if (displayedPage.editUser) {
                return <AddUser triggerReturn={showUserListPage} editingUser={editTarget} />
            }
        }
        else {
            return <div>Loading Spinner...</div>
        }
    }

    useEffect(() => {
        getAccountId();
    }, []);

    return (
        <div>
            <PageHeader>
                <PageHeaderTitle title="User Management" className="pf-u-mb-lg" />
                <p> Manage organization users and privelages</p>
            </PageHeader>
            <Divider/>
            <PageSection>
                <QueryContext.Provider value={queryDetails}>
                    <BaseRender/>
                </QueryContext.Provider>
            </PageSection>

        </div>

    )
}

export default UserManagementController;
