import './UserList.scss';
import React, {useEffect, useState, useContext} from 'react';
import { TableComposable, Thead, Tr, Th, Tbody, Td } from '@patternfly/react-table';
import {Toolbar, ToolbarItem, ToolbarContent, SearchInput, Button, Pagination, PaginationVariant, Checkbox} from '@patternfly/react-core';
import {QueryContext} from './UserManagementController';
import PropTypes from 'prop-types';

const UserList = (props) => {
    const queryDetails = useContext(QueryContext);
    const [listData, setListData] = useState([]);
    const [checkedUsers, setCheckedUsers] = useState({});
    // eslint-disable-next-line no-magic-numbers
    const [perPageValue, setPerPageValue] = useState(10);
    // eslint-disable-next-line no-magic-numbers
    const [currentPage, setCurrentPage] = useState(0);
    // eslint-disable-next-line
    const [resultsCount, setResultsCount] = useState(20)

    const pullUserList = () => {
        let targetUrl = queryDetails.accountAPI + "/" + queryDetails.accountID + "/users?"
        targetUrl += new URLSearchParams({
            firstResultIndex: currentPage,
            maxResults: perPageValue
        })
        fetch(targetUrl, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + queryDetails.userToken
            }
        })
            .then(response => response.json())
            .then(userList => {
                setListData(userList.body);

                const checkReference = {}
                // iterate through the result list to populate checked status
                for (let i = 0; i < userList.body.length; i++) {
                    const userObj = {
                        status: userList.body[i].user.status,
                        username: userList.body[i].user.username,
                        checked: false
                    }
                    const userId = userList.body[i].user.id

                    checkReference[userId] = userObj;
                }
                setCheckedUsers(checkReference);
            })
    }

    /**
     * On load pull the user list
     */
    useEffect(() => {
        pullUserList();
    }, [currentPage]);

    const doNothing = () => {
        console.log("doing nothing");
    }

    /**
     *
     */
    const disableUser = () => {
        let targetForDisable;
        const checkedList = Object.keys(checkedUsers);
        for (let i = 0; i < checkedList.length; i++) {
            if (checkedUsers[checkedList[i]].checked) {
                targetForDisable = checkedList[i];
                break;
            }
        }

        if (targetForDisable) {
            // eslint-disable-next-line no-useless-concat
            fetch(queryDetails.accountAPI + "/" + queryDetails.accountID + "/users" + "/" + targetForDisable + "/status", {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + queryDetails.userToken
                },
                body: JSON.stringify({
                    "status": "disabled"
                })

            })
                .then(response => response.json())
                .then(() => {
                    // fire the queries required to disable all selected users
                    window.location.reload(false);
                })
        }
    }

    /**
     * triggered by the edit user button, this function calls the prop passed in that will swap the view to
     * use edit.
     */
    const editUser = () => {
        let targetForEdit;
        const checkedList = Object.keys(checkedUsers);
        for (let i = 0; i < checkedList.length; i++) {
            if (checkedUsers[checkedList[i]].checked) {
                targetForEdit = checkedList[i];
                break;
            }
        }

        if (targetForEdit) {
            // eslint-disable-next-line no-useless-concat
            fetch(queryDetails.accountAPI + "/" + queryDetails.accountID + "/users" + "/" + targetForEdit, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + queryDetails.userToken
                },

            })
                .then(response => response.json())
                .then(resp => {
                    // fire the queries required to disable all selected users
                    props.triggerEditUser(resp.body);
                })
        }
    }

    /**
     * Governs what happens when a user clicks a checkbox. Manages the checkbox useState status
     */
    const handleCheckbox = (id, checked) => {

        // get the existing state
        const existing = checkedUsers[id];

        // if we know something has been checked then display the 'disable user', and 'edit user' buttons
        if (checked) {
            // reset with checked
            existing.checked = true;
        }
        else {
            existing.checked = false;
        }

        setCheckedUsers({...checkedUsers, [id]: existing});


    }

    /**
     *
     */
    const handleNextPageClick = () => {
        setCurrentPage(currentPage + perPageValue);
    }

    /**
     *
     */
    const handlePrevPageClick = () => {
        setCurrentPage(currentPage - perPageValue);
    }

    const ConditionRender = () => {
        // eslint-disable-next-line no-magic-numbers
        if (listData.length > 0) {
            return <div>
                        <Toolbar id="userListToolbar">
                            <ToolbarContent>
                                <ToolbarItem>
                                    <SearchInput aria-label="Search user list"/>
                                </ToolbarItem>
                                <ToolbarItem>
                                    <Button onClick={props.triggerAddUser} variant="primary">Add User</Button>
                                </ToolbarItem>
                                <ToolbarItem>
                                    <Button onClick={editUser} variant="primary">Edit User</Button>
                                </ToolbarItem>
                                <ToolbarItem>
                                    <Button onClick={disableUser} variant="link">Disable User</Button>
                                </ToolbarItem>
                            </ToolbarContent>
                        </Toolbar>
                        <TableComposable aria-label="Sortable table">
                            <Thead>
                                <Tr>
                                    <Th
                                        select={{
                                            onSelect: () => doNothing(),
                                            isSelected: doNothing()
                                        }}
                                    />
                                    <Th>Name</Th>
                                    <Th modifier="wrap">Email</Th>
                                    <Th modifier="wrap" info={{ tooltip: 'More information ' }}>
                                        LastName
                                    </Th>
                                    <Th modifier="wrap">First Name</Th>
                                    <Th modifier="wrap">Status</Th>
                                </Tr>
                            </Thead>
                            <Tbody>
                                {listData.map((userRecord) => (
                                    <Tr key={userRecord.user.username}>
                                        <Td>
                                            <Checkbox
                                                onChange={(val) => handleCheckbox(userRecord.user.id, val)}
                                                isChecked={checkedUsers[userRecord.user.id].checked}
                                                id={userRecord.user.id + '-check'}
                                            />
                                        </Td>
                                        <Td>{userRecord.user.username}</Td>
                                        <Td>{userRecord.user.email}</Td>
                                        <Td>{userRecord.user.lastName}</Td>
                                        <Td>{userRecord.user.firstName}</Td>
                                        <Td>{userRecord.user.status}</Td>
                                    </Tr>
                                ))}
                            </Tbody>
                        </TableComposable>
                        <Pagination
                            perPageComponent="button"
                            widgetId="bottom-pagination"
                            itemCount={resultsCount}
                            perPage={perPageValue}
                            variant={PaginationVariant.bottom}
                            onPerPageSelect={(selection) => { setPerPageValue(selection) }}
                            onNextClick={handleNextPageClick}
                            onPreviousClick={handlePrevPageClick}
                            page={currentPage}
                        />
                    </div>

        }

        return <div>spinner ....</div>

    }

    return (
        <div>
            <ConditionRender/>
        </div>
    )
}

UserList.propTypes = {
    triggerEditUser: PropTypes.func,
    triggerAddUser: PropTypes.func

}

export default UserList;
