import React from 'react';
import {Dropdown, DropdownItem, DropdownToggle} from "@patternfly/react-core";
import PropTypes from 'prop-types';


/**
 * A subclass to handle all of the bloat that comes with this under patternfly design
 * @param props primarily intended to  pass the array of items provided to it
 * @constructor
 */
const DropdownHandler = (props) => {
    const [isDDOpen, setIsDDOpen] = React.useState(false);
    const [selection, setSelection] = React.useState(props.defaultText);

    const onDDToggle = (isOpen) => {
        setIsDDOpen(isOpen);
    };

    const onDDFocus = () => {
        const element = document.getElementById(props.idValue);
        element.focus();
    };
    const onDDSelect = () => {
        setIsDDOpen(false);
        onDDFocus();
    };

    return (
        <Dropdown
            onSelect={onDDSelect}
            toggle={
                <DropdownToggle id={props.idValue} onToggle={onDDToggle}>
                    {selection}
                </DropdownToggle>
            }
            isOpen={isDDOpen}
            dropdownItems={props.items.map((val) => {
                return <DropdownItem key={val} onClick={ () => { props.onSelect(val); setSelection(val); }}>{val}</DropdownItem> })}
        />
    )
}

DropdownHandler.propTypes = {
    defaultText: PropTypes.string,
    idValue: PropTypes.string,
    items: PropTypes.array,
    onSelect: PropTypes.func
}
export default DropdownHandler;
