import React, { useState, useEffect } from 'react';
import UserManagementController from "../../Components/userManagement/UserManagementController";
import {isAdminUser} from "../../Components/utils/general-utils";
import DisabledPage from "../../Components/disabledPage/disabled-page";


const UserManagement = () => {
    let userToken = null;
    const [currentUser, updateUser] = useState({
        adminUser: true,
        renderedPage: <UserManagementController />
    })
    const isSelfServiceIDPEnabled = true;
    // useFlag('iamconsole.IDPIntegration.enabled');

    useEffect(() => {
        // insights?.chrome?.appAction?.('identity-provider-integration');
        // eslint-disable-next-line no-undef
        insights.chrome.auth.getToken().then(token => {
            userToken = token;
            // if the user is in fact and admin user then render TwoFactorCheck
            if (isAdminUser(userToken) && isSelfServiceIDPEnabled) {
                updateUser({
                    adminUser: true,
                    renderedPage: <UserManagementController />
                })
            }

            // check to see if 2fa feature flag is set. if not then render the proper error page
            if (!isSelfServiceIDPEnabled) {
                updateUser({
                    adminUser: false,
                    renderedPage: <DisabledPage/>
                })
            }

        })
    }, []);


    return (
        <React.Fragment>
            {currentUser.renderedPage}
        </React.Fragment>
    )
}

export default UserManagement;
